.form-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 100%;

  h1 {
    font-size: 2.5rem;
  }

  .form-items {
    display: flex;
    flex-direction: column;
    width: 32rem;
    padding: 2rem;
    border: 0.1rem #c0c0c0 solid;
    border-radius: 0.5rem;
    list-style-type: none;

    li {
      display: flex;
      flex-direction: column;
      margin-bottom: 1rem;
      margin-top: 1rem;
    }
  }
}
